.nav-links {
    list-style: none;
   
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: end;
    align-items: center;
}

.nav-links li{
    margin: 1rem;
}

.nav-links a {
    border: 1px solid transparent;
    color: #292929;
    text-decoration: none;
    
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a:active {
 color: pink;
 
 
}